main {
  color: #333;
  background-color: #f4f4f4;
  padding: 20px;
  font-family: Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
  line-height: 1.6;
}

.container {
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 8px;
  max-width: 800px;
  margin: 0 auto;
  overflow: hidden;
  box-shadow: 0 0 10px #0000001a;
}

section {
  border-bottom: 1px solid #ddd;
  margin-bottom: 20px;
  padding: 20px;
}

.scientist {
  margin-bottom: 20px;
}

.name {
  color: #1e90ff;
  font-size: 1.5em;
  font-weight: bold;
}

.research-areas {
  color: #555;
  margin-top: 10px;
  font-style: italic;
}

.research-contact-info {
  margin-top: 10px;
}

a {
  color: #007bff;
  text-decoration: none;
  transition: color .3s;
}

a:hover {
  color: #0056b3;
}

